<template>
  <div class="my">
    <div
      class="bg"
      :style="{ backgroundImage: 'url(' + userInfo.userBg + ')' }"
    >
      <div class="person">
        <div class="avatar">
          <img v-if="isLogin" class="auto-img" :src="userInfo.userImg" alt="" />
          <div class="avatar-text" v-else @click="goPage('Login')">
            点击登陆
          </div>
        </div>
        <div class="nickname">{{ userInfo.nickName }}</div>
      </div>
      <div class="desc">
        {{ userInfo.desc == "" ? "今天也是随幸的一天" : userInfo.desc }}
      </div>
      <div class="mybg" v-if="isLogin">
        <span>更换背景</span>
        <van-uploader class="v-bg" :after-read="upload"></van-uploader>
      </div>
    </div>
    <van-cell-group>
      <van-cell
        v-for="(item, index) in cellData"
        :key="index"
        :title="item.title"
        size="large"
        @click="goPage(item.name)"
        is-link
      />
    </van-cell-group>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { utils } from "../utils/utils";
export default {
  name: "My",
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
    ...mapState("myModule", ["cellData", "userInfo"]),
  },
  created() {
    this.$store.commit("myModule/changeData", { key: "userInfo", value: {} });
    this.getUserInfo();
  },
  methods: {
    goPage(name) {
      //判断是否有登陆
      if (!this.isLogin) {
        this.noTokenLogin();
        return;
      }
      this.$router.push({ name });
    },

    //获取个人信息
    getUserInfo() {
      let tokenString = localStorage.getItem("_t");

      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在加载...",
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((d) => {
          this.$toast.clear();

          this.$store.commit("changeData", {
            key: "isLogin",
            value: d.data.code != 700,
          });
          if (d.data.code == 700) {
            return;
          }

          this.$store.commit("myModule/changeData", {
            key: "userInfo",
            value: d.data.result[0],
          });
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //上传头像
    upload(file) {
      //判断是否有登陆
      if (!this.isLogin) {
        this.noTokenLogin();
        return;
      }

      //控制图片大小不超过500kb
      let imgSize = 500 * 1024;
      if (file.file.size > imgSize) {
        this.$toast("上传图片大小不能超过500KB");
        return;
      }

      //将图片上传到服务器
      //获取图片类型
      let imgType = file.file.type.split("/")[1];
      //处理图片的base64
      let serverBase64Img = file.content.replace(
        /data:image\/[A-Za-z]+;base64,/,
        ""
      );

      let tokenString = localStorage.getItem("_t");
      let url = "";

      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在加载...",
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img,
        },
        transformRequest: utils.transformRequest,
      })
        .then((d) => {
          this.$toast.clear();

          this.userInfo.userBg = d.data.userBg;
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //游客登陆
    noTokenLogin() {
      this.$dialog
        .confirm({
          title: "前往登陆",
          message: "您还没有登陆，请问您要前往登陆界面吗",
        })
        .then((text) => {
          if (text == "confirm") {
            this.$router.push({ name: "Login" });
          }
        })
        .catch(() => {});
    },
  },
};
</script>

<style lang="less" scoped>
.my {
  .bg {
    height: 80px;
    background-color: #ddd;
    padding: 60px 20px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    position: relative;
    .mybg {
      position: absolute;
      top: 20px;
      right: 20px;
      color: #fff;
      font-size: 14px;
    }
    .v-bg {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
      opacity: 0;
      z-index: 2;
    }
    .person {
      display: flex;
      .avatar {
        width: 80px;
        height: 80px;
        background-color: #fff;
        border-radius: 50%;
        overflow: hidden;
      }
      .avatar-text {
        color: #165dad;
        font-size: 16px;
        line-height: 80px;
        height: 80px;
        text-align: center;
        width: 64px;
        margin: 0 auto;
      }
      .nickname {
        color: #fff;
        font-size: 16px;
        margin-left: 20px;
        line-height: 80px;
      }
    }
    .desc {
      color: #fff;
      font-size: 14px;
      margin-top: 8px;
      margin-left: 100px;
    }
  }
}
</style>